<template>
<div>
    <pa :books='books' :boos='boos'>
    <template v-slot:header>女频新书
  </template>
    </pa>
</div>
</template>

<script>
import Pa from '../core/pa.vue'
export default {
  name: 'GirlNew',
  components: {
    Pa
  },
  data () {
    return {
      books: null
    }
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    async getHomeInfo () {
      let data = await fetch('https://scxs.pysmei.com/Categories/6/hot/1.html')
      let bbb = await data.text()
      let fff = await JSON.parse(bbb)
      let ddd = await fff.data.BookList
      this.books = ddd
    }
  }
}
</script>

