<template>
<div>
    <pa-a :books='books'>
    <template v-slot:header>精品推荐
  </template>
    </pa-a>
</div>
</template>

<script>
import PaA from '../core/pa.vue'
import { EventBus } from '../../assets/Bus'
import axios from 'axios'
export default {
  name: 'HomeRecommand',
  components: {
    PaA
  },
  data () {
    return {
      books: null,
      loading: true
    }
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    async getHomeInfo () {
      let data = await fetch('https://scxs.pysmei.com/Categories/6/hot/1.html')
      let bbb = await data.text()
      let fff = await JSON.parse(bbb)
      let ddd = await fff.data.BookList
      this.books = ddd
      EventBus.$emit('loading', this.loading)
    },
    getHomeInfo1 () {
      axios
        .get('/apo/node/info?nodeAlias=rankinglist-male-hot-month')
        .then(response => {
          this.books = response.data.books
          this.loading = 'flase'
          EventBus.$emit('loading', this.loading)
        })
    }
  }

}
</script>

